import React from 'react';

import useAsync from '../../hooks/useAsync';
import CategoryServices from '../../services/CategoryServices';


const ParentCategory = ({showId = false}) => {
  const { data } = useAsync(CategoryServices.getAllCategory); //   console.log(value);
  return (
    <>
      {showId ? data.length > 0 && data.map((parent) => (
        <option key={parent._id} value={parent._id}>
          {parent.categoryname}
        </option>
      )) : data.length > 0 && data.map((parent) => (
        <option key={parent._id} value={parent.categoryname}>
          {parent.categoryname}
        </option> ))}
    </>
  );
};

export default ParentCategory;
