import { useContext, useEffect, useState } from "react";
import { useForm } from "react-hook-form";
import { SidebarContext } from "../context/SidebarContext";
import ProductServices from "../services/ProductServices";
import { notifyError, notifySuccess } from "../utils/toast";

const useProductSubmit = (id) => {
  const [imageUrl, setImageUrl] = useState([]);
  const [children, setChildren] = useState("");
  const [tag, setTag] = useState([]);
  const [metatag, setMetaTag] = useState(false);
  const [showRental, setShowRental] = useState([]);
  const { isDrawerOpen, closeDrawer, setIsUpdate } = useContext(SidebarContext);

  const {
    register,
    handleSubmit,
    watch,
    setValue,
    clearErrors,
    formState: { errors },
  } = useForm();

  const onSubmit = (data) => {
    console.log("data", data);
    if (imageUrl.length <= 0) {
      notifyError("Image is required!");
      return;
    }

    if (data.originalPrice < data.salePrice) {
      notifyError("SalePrice must be less then or equal of product price!");
      return;
    }

    const productData = {
      title: data.title,
      slug: data.slug
        ? data.slug
        : data.title.toLowerCase().replace("&", "").split(" ").join("-"),
      description: data.description,
      category: data.category,
      children: data.children ? data.children : " ",
      parent: data.parent ? data.parent : " ",
      color: data.color,
      type: data.type ? data.type : " ",
      quantity: data.quantity,

      originalPrice: data.originalPrice,
      price: data.price ? data.price : data.originalPrice,
      discount:
        data.price > 0 &&
        ((data.originalPrice - data.price) / data.originalPrice) * 100,
      // imageDetails: imageUrl,
      image: JSON.stringify(imageUrl),
      tag: JSON.stringify(tag),
      unit: data.unit,
      status: data.status ? data.status : "Show",
    };
    console.log(productData, "========Pd");
    if (id) {
      ProductServices.updateProduct(id, productData)
        .then((res) => {
          setIsUpdate(true);
          notifySuccess(res.message);
        })
        .catch((err) => notifyError(err.message));
      closeDrawer();
    } else {
      ProductServices.addProduct(productData)
        .then((res) => {
          setIsUpdate(true);
          notifySuccess(res.message);
        })
        .catch((err) => notifyError(err.message));
      closeDrawer();
    }
  };

  useEffect(() => {
    if (!isDrawerOpen) {
      setValue("title");
      setValue("slug");
      setValue("description");
      setValue("parent");
      setValue("category");
      setValue("children");
      setValue("type");
      setValue("unit");
      setValue("quantity");
      setValue("originalPrice");
      setValue("material");
      setValue("color");
      setValue("price");
      setImageUrl("");
      setChildren("");
      setTag([]);
      setMetaTag([]);
      clearErrors("title");
      clearErrors("slug");
      clearErrors("description");
      clearErrors("parent");
      clearErrors("children");
      clearErrors("type");
      clearErrors("unit");
      clearErrors("quantity");
      clearErrors("originalPrice");
      clearErrors("price");
      clearErrors("material");
      clearErrors("color");
      clearErrors("status");
      return;
    }

    if (id) {
      ProductServices.getProductById(id)
        .then((res) => {
          // console.log(JSON.parse(res.tags));
          if (res) {
            // setShowRental(res.rentalStatus);
            // setValue("rentalPrice", res.rentalPrice);
            setImageUrl(res.imageDetails);
            setValue("title", res.title);
            setValue("slug", res.slug);
            setValue("description", res.description);
            setValue("category", res.category);
            setValue("type", res.category_type);
            setValue("unit", res.unit);
            setValue("quantity", res.quantity);
            setValue("originalPrice", res.cost);
            setValue("price", res.price);
            // setValue("unit", res.length_class);
            setValue("color", res.color);
            setValue("children", res.children);
            setValue("parent", res.parent);
            setValue("material", res.material);
            setValue("status", res.status);
            if (res.tags) {
              setTag(JSON.parse(res.tags));
            }
            if (res.metatag) {
              setMetaTag(JSON.parse(res.metatag));
            }
            // setImageUrl(res.image[0].path);
          }
        })
        .catch((err) => {
          console.log(err);
          notifyError("There is a server error edit!");
        });
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [id, setValue, isDrawerOpen]);

  useEffect(() => {
    setChildren(watch("children"));
  }, [watch, children]);

  return {
    register,
    watch,
    handleSubmit,
    onSubmit,
    errors,
    imageUrl,
    setImageUrl,
    tag,
    metatag,
    setTag,
    setMetaTag,
    showRental,
    setShowRental,
  };
};

export default useProductSubmit;
