import requests from './httpService';

const CategoryServices = {
  getAllCategory() {
    return requests.get('/categories?admin=1');
  },

  getCategoryById(id) {
    return requests.get(`/category/${id}?admin=1`);
  },

  addCategory(body) {
    return requests.post('/category/add?admin=1', body);
  },

  updateCategory(id, body) {
    return requests.put(`/category/${id}?admin=1`, body);
  },

  updateStatus(id, body) {
    return requests.put(`/category/status/${id}?admin=1`, body);
  },

  deleteCategory(id, body) {
    return requests.patch(`/category/${id}?admin=1`, body);
  },
};

export default CategoryServices;
